import React from 'react';
import './aboutme.css';


export default function AboutMe() {


    return (
        <div className="aboutme-container">
            <div className="aboutme-content">
                <div className="aboutme-title">
                    <h4>O mnie</h4>
                </div>
                <div className="aboutme-text">
                    <p>Jestem absolwentem informatyki na Uniwersytecie Mikoaja Kopernika w Toruniu. 
                        Niedawno obroniem tytu inyniera informatyki prac opart na uczeniu maszynowym oraz splotowych sieciach neuronowych. 
                        Obecnie staram si o tytu magistra na tym samym uniwersytecie. 
                        Przez technikum i studia cigny si za mn najrniejsze jzyki programowania, 
                        jednak najbardziej zainteresowaa mnie sfera WebApp i MobileApp developmentu. 
                        Swoj przygod z aplikacjami zaczem w 2019 roku tworzc, w oparciu o React.js, projekt zespoowy JudiBell,
                        ktry zaj drugie miejsce w rankingu pod wzgldem potencjau biznesowego. Obecnie rozwijam swoje umiejtnoci we Flutter, 
                        GOlang oraz framework'ach Django, Spring oraz Node.js. </p>
                </div>
            </div>
        </div>
    )
}